<template>
  <div>
    <div class="searchs">
      <el-row>
        <el-col>
          <router-link to="/image/create/banner">
            <el-button style="margin-left: 8px;" type="success">创建</el-button>
          </router-link>
        </el-col>
      </el-row>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="名称" align="center" width="200" />
      <el-table-column prop="sort" label="排序" align="center" width="100" />
      <el-table-column prop="url" label="地址" align="center" width="100" />
      <el-table-column
        prop="status_name"
        label="状态"
        align="center"
        width="100"
      />
      <el-table-column prop="uptime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="eidt(scope.row.id)"
            >编辑</el-button
          >
          <el-button type="primary" size="small" @click="remove(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        name: '',
        status: '',
        type: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        type: null,
        status: null,
      },
      id: 0,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    remove(id) {
      this.loading = true
      Fecth('/sys/image/remove', {
        id: id,
      })
        .then(() => {
          this.loading = false
          this.FetchList()
        })
        .catch(() => (this.loading = false))
    },
    eidt(id) {
      this.$router.push('/image/update/' + id)
    },

    FetchList() {
      this.loading = true
      Fecth('/sys/image/search', {
        page_curren: this.page_curren,
        ...this.searchs,
        type: 'banner',
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
